.welcome-wrapper {
  position: absolute;
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(90deg, #0072ff, #ff0058);
  overflow: hidden;
}

.container {
  display: flex;
  justify-content: center;
}

.pages {
  position: relative;
  display: flex;
  max-width: 700px;
  min-height: 330px;
  flex-wrap: wrap;
  margin-top: 150px;
  transition: 0.5s;
}

.link-container-url {
  text-decoration: none;
  min-height: 330px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pages .link-container {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #03a9f4;
  width: 300px;
  height: 330px;
  transition: 0.5s;
}

.pages .link-container:hover {
  filter: blur(0);
  transform: scale(1);
}

.pages .link-container::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #03a9f4;
  z-index: 1;
  transition: 0.5s;
}

.pages .link-container:hover::before {
  background: #000;
  transform: scale(1.25);
}

.pages .link-container.js-page:hover::before {
  background: url(https://i.imgur.com/RozaFBV.jpg?1);
  transform: scale(1.25);
}

.pages .link-container.user-page:hover::before {
  background: url(https://i.imgur.com/3OnzgrX.jpg?1);
  background-repeat: no-repeat;
  background-size: 300px 330px;
  transform: scale(1.25);
}

.link-container-url:nth-child(1) {
  margin-right: 40px;
}
.link-container-url:nth-child(2) {
  margin-left: 40px;
}

.pages .link-container .content {
  text-align: center;
  background-color: #03a9f4;
  color: #000;
  text-decoration: none;
  z-index: 2;
  padding: 0 20px;
  transition: 0.5s;
}

.pages .link-container:hover .content {
  color: #fff;
}

.pages .link-container .content h2 {
  font-size: 1.8em;
}

.pages .link-container .content p {
  font-size: 0.9em;
}

@media (max-width: 710px) {
  .pages {
    flex-direction: column;
    margin-top: 50px;
  }

  .link-container-url:nth-child(1) {
    margin-right: 0;
  }
  .link-container-url:nth-child(2) {
    margin-left: 0;
    margin-top: 40px;
  }

  .pages .link-container::before {
    content: none;
  }
}

.icons {
  position: absolute;
  display: flex;
  justify-content: center;
  max-width: 1650px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
}

.skill-icon {
  position: absolute;
  background: aliceblue;
  transform-style: preserve-3d;
  animation: animate linear infinite;
}

.skill_icon-html {
  width: 150px;
  height: 150px;
  left: 100px;
  animation-duration: 5s;
}

.skill_icon-css {
  width: 100px;
  height: 100px;
  left: 300px;
  animation-duration: 10s;
}

.skill_icon-js {
  width: 250px;
  height: 250px;
  left: 600px;
  animation-duration: 15s;
}

@keyframes animate {
  0% {
    transform: translateY(110vh);
  }
  100% {
    transform: translateY(-20vh);
  }
}
